    // Contact Page - Section 4 (FAQ)
    contactFaqTitle: 'الأسئلة المتكررة',
    contactFaqSubtitle: 'إجابات سريعة على الأسئلة الشائعة حول خدماتنا',
    contactFaq1Question: 'كم من الوقت مقدماً يجب أن أحجز خدماتكم؟',
    contactFaq1Answer: 'نوصي بالحجز قبل 3-6 أشهر على الأقل للفعاليات الكبرى مثل حفلات الزفاف والمؤتمرات. ومع ذلك، يمكننا استيعاب جداول زمنية أقصر اعتماداً على التوفر وتعقيد الحدث. اتصل بنا في أقرب وقت ممكن لمناقشة احتياجاتك.',
    contactFaq2Question: 'ما الذي يتضمنه باقات تخطيط الفعاليات الخاصة بكم؟',
    contactFaq2Answer: 'تختلف باقاتنا بناءً على احتياجاتك ولكنها تتضمن عادةً اختيار المكان، تنسيق الموردين، إدارة الميزانية، إنشاء الجدول الزمني، تخطيط التصميم والديكور، التنسيق في يوم الحدث، والمتابعة بعد الحدث. نقدم باقات قابلة للتخصيص لتتناسب مع متطلباتك المحددة.',
    contactFaq3Question: 'هل تقدمون خدمات خارج منطقتكم المحلية؟',
    contactFaq3Answer: 'نعم! نقدم خدمات إدارة الفعاليات على المستوى الوطني والدولي. لدينا شبكة من الشركاء الموثوقين في جميع أنحاء العالم ويمكننا إدارة فعاليات الوجهات. قد تنطبق تكاليف السفر والإقامة على الفعاليات خارج منطقة خدمتنا المحلية.',
    contactFaq4Question: 'هل يمكنني تخصيص باقات الخدمة الخاصة بكم؟',
    contactFaq4Answer: 'بالتأكيد! كل حدث فريد من نوعه، ونعتقد أن باقة الخدمة الخاصة بك يجب أن تكون كذلك. نقدم باقات مرنة وقابلة للتخصيص يمكن تصميمها لتتناسب مع احتياجاتك وميزانيتك ورؤيتك المحددة. دعونا نناقش ما يناسبك.',
    contactFaq5Question: 'ما هي سياسة الإلغاء الخاصة بكم؟',
    contactFaq5Answer: 'تختلف سياسة الإلغاء الخاصة بنا اعتماداً على الباقة والتوقيت. بشكل عام، الودائع غير قابلة للاسترداد، ولكننا نقدم خيارات إعادة جدولة مرنة. سنقدم شروطاً مفصلة في اتفاقية الخدمة الخاصة بك وسنعمل معك لإيجاد أفضل حل.',
